$mainColor1: #D5E27B;
$mainColor2: #004822;

.main-color1 {
   color: $mainColor1;
}

.main-color2 {
   color: $mainColor2;
}

// header

.navbar {
   background-color: #fff;
   font-size: 15px;
   line-height: 21px;
   color: #25201F;
}

.navbar-nav {
   white-space: nowrap;
   gap: 24px;
}

.btn {
   border-radius: 60px
}

.btn-headerV1 {

   &:hover {
      background: #a6b160;
   }
}

.btn-headerV2 {
   background-color: $mainColor1;

   &:hover {
      background: #a6b160;
   }
}

// header

// main
.main {
   padding-top: 116px;
   background: url('../images/main/bg-IMAGE.jpg') center/cover no-repeat;
   // .main__subtitle

   &__subtitle {
      font-size: 24px;
      line-height: 29px;

   }

   // .main__title

   &__title {
      font-weight: 700;
      font-size: 64px;
      line-height: 54px;
      text-transform: uppercase;
      min-width: 260px;

      @media (max-width:768px) {
         font-size: 30px;
         line-height: 34px;
      }
   }

   // .main__text

   &__text {
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;

      // .main__text_small

      &_small {
         font-size: 12px;
         line-height: 14px;
      }
   }
}

.main-bage {
   justify-content: flex-end;

   @media (max-width: 768px) {
      justify-content: center;
   }
}

.main-bage2 {
   justify-content: flex-end;

   @media (max-width: 768px) {
      justify-content: center;
   }
}

.main-card1 {
   width: 252px;
   background: $mainColor2;
   border-radius: 0;
}

.main-card2 {
   width: 252px;
   background: $mainColor1;
   border-radius: 0;
}

// main

// poster
.poster {
   padding: 25px 0px;
   background: #25201F;
}

.poster-row {
   @media (max-width: 992px) {
      gap: 20px;
   }
}

.poster-text {

   // .poster-text_star

   &_star {
      padding: 12.5px 0px;
      background: url('../images/poster/stars.svg') center no-repeat;
   }

   // .poster-text_small

   &_small {
      position: relative;
      padding-left: 35px;

      &::before {
         content: '';
         background: url('../images/poster/check_small.svg') no-repeat;
         width: 25px;
         height: 25px;
         position: absolute;
         left: 0;
      }
   }

   // .poster-text_big

   &_big {
      position: relative;
      padding-left: 55px;


      &::before {
         content: '';
         background: url('../images/poster/check_big.svg') no-repeat;
         width: 45px;
         height: 40px;
         position: absolute;
         left: 0;
         top: -7px;
      }
   }
}

// poster

.fixed-block {
   z-index: -1;
   height: 660px;
   position: sticky;
   top: 0;
   left: 0;

   &_v1 {
      background: url('../images/fixed/bg_IMAGE.jpg') 0 0/cover no-repeat;
   }

   &_v2 {
      background: url('../images/fixed/bg_IMAGE_2.jpg') 0 0/cover no-repeat;
   }

   &_v3 {
      background: url('../images/fixed/bg_IMAGE_3.jpg') 0 0/cover no-repeat;
   }
}

.beyond {
   padding: 97px 0;
   background: #fff;
}

.beyond-title {
   margin-bottom: 94px;
}

.title {
   font-size: 43px;
   line-height: 43px;
}

.beyond-text {
   position: relative;
   padding-top: 35px;
   text-align: center;
   max-width: 252px;

   &::before {
      content: '';
      background: url('../images/poster/check_small.svg') no-repeat;
      width: 25px;
      height: 25px;
      position: absolute;
      left: 45%;
      top: 0px;
   }
}


.cash {
   background: $mainColor2;
   padding: 77px 0px;
}

.cash-title {
   max-width: 458px;
   margin: 0 auto;
}

.cash-text {
   max-width: 826px;
   margin: 0 auto;
   font-size: 18px;
   line-height: 25px;
}

.cash-col-block {
   max-width: 400px;
   position: relative;
   padding-left: 70px;

   &_v1 {

      &::before {
         content: '1';
         position: absolute;
         color: $mainColor1;
         top: 10px;
         left: 20px;
         font-size: 38px;
         line-height: 34px;
      }

      &::after {
         content: '';
         width: 60px;
         height: 60px;
         position: absolute;
         border: 2px solid $mainColor1;
         top: 0;
         left: 0;
      }
   }

   &_v2 {

      &::before {
         content: '2';
         position: absolute;
         color: $mainColor1;
         top: 10px;
         left: 20px;
         font-size: 38px;
         line-height: 34px;
      }

      &::after {
         content: '';
         width: 60px;
         height: 60px;
         position: absolute;
         border: 2px solid $mainColor1;
         top: 0;
         left: 0;
      }
   }

   &_v3 {

      &::before {
         content: '3';
         position: absolute;
         color: $mainColor1;
         top: 10px;
         left: 20px;
         font-size: 38px;
         line-height: 34px;
      }

      &::after {
         content: '';
         width: 60px;
         height: 60px;
         position: absolute;
         border: 2px solid $mainColor1;
         top: 0;
         left: 0;
      }
   }

   &_v4 {

      &::before {
         content: '4';
         position: absolute;
         color: $mainColor1;
         top: 10px;
         left: 20px;
         font-size: 38px;
         line-height: 34px;
      }

      &::after {
         content: '';
         width: 60px;
         height: 60px;
         position: absolute;
         border: 2px solid $mainColor1;
         top: 0;
         left: 0;
      }
   }
}

.cash-col-title {
   font-size: 38px;
   line-height: 34px;
}

.cash-col-text {
   font-size: 16px;
   line-height: 24px;
}

.cash-col-image {
   max-width: 100%;
}

.feels {
   padding: 97px 0 0;
   background: #fff;
}

.feels-title {
   color: $mainColor2;
   max-width: 494px;
   margin: 0 auto 0;
}

.feels-col-block {
   padding: 331px 0 59px;
   max-width: 285px;

   @media (max-width: 992px) {
      border: 0.1px solid #000;
   }

   // .feels-col-block_v1

   &_v1 {
      background: url('../images/feels/feels_1.jpg') no-repeat;
   }

   // .feels-col-block_v2

   &_v2 {
      background: url('../images/feels/feels_2.jpg') no-repeat;
   }

   // .feels-col-block_v3

   &_v3 {
      background: url('../images/feels/feels_3.jpg') no-repeat;
   }

   // .feels-col-block_v4

   &_v4 {
      background: url('../images/feels/feels_4.jpg') no-repeat;
   }
}

.feels-col-box {
   background: rgba(0, 0, 0, 0.25);
   padding: 5px;
}

.feels-col-title {
   font-size: 28px;
   line-height: 28px;
   color: $mainColor2;
}

.protected {
   padding: 75px 0 98px;
   background: #25201F;
}

.protected-text {
   margin-bottom: 100px;
}


.protected-col-text {
   position: relative;
   max-width: 225px;
   padding-top: 45px;

   &::before {
      content: '';
      background: url('../images/poster/check_small.svg') no-repeat;
      position: absolute;
      top: 0;
      left: 45%;
      width: 25px;
      height: 25px;
   }
}

.kind {
   padding: 100px 0;
   background: #fff;
}

.carousel-inner {
   background: #5A530C;
}

.carousel-item {}

.carousel-block {
   @media (max-width:992px) {
      flex-wrap: wrap;
      justify-content: center;
   }
}

.carousel-box {
   display: flex;
   flex-direction: column;
   gap: 20px;
}

.carousel-text {
   flex: 1 1 auto;
   font-size: 20px;
   line-height: 24px;

   @media (max-width: 768px) {
      font-size: 18px;
      line-height: 22px;
   }
}

.carousel-container {
   display: flex;
   align-items: center;
   justify-content: space-between;
   flex-wrap: wrap;

   @media (max-width: 768px) {
      gap: 20px;
      justify-content: center;
   }
}

.carousel-container-item {
   display: flex;
   gap: 20px;
}

.carousel-picture {
   max-width: 100%;
}

.carousel-container-image {
   border-radius: 100%;
}

.carousel-container-paragraph {
   max-width: 211px;
}

.carousel-container-name {
   font-size: 18px;
   line-height: 25px;
}

.carousel-container-text {
   font-size: 16px;
   line-height: 19px;
}

.ready {
   padding: 100px 0;
   background: $mainColor2;
}

.btn-ready {
   background: $mainColor1;
   max-width: 175px;
   display: block;
   margin: 0 auto;
   font-size: 18px;
   line-height: 21px;

   &:hover {
      background: #a6b160;
   }
}

ul,
li,
a {
   list-style: none;
   padding: 0;
   margin: 0;
   text-decoration: none;
}

.footer {
   background: $mainColor2;
   padding: 111px 0 20px;
   border-top: 1px solid #fff;
}

.footer-col-title {
   font-size: 20px;
   line-height: 24px;
   font-weight: 400;
   margin-bottom: 15px;
}

.footer-col-list {
   font-size: 16px;
   line-height: 21px;
   display: flex;
   flex-direction: column;
   gap: 5px;
}

.footer-col-link {
   color: #fff;
   transition: color 0.3s ease;

   &:hover {
      color: $mainColor1;
   }
}

.footer-icon {
   transition: transform 0.3s ease;

   &:hover {
      transform: scale(1.2);
   }
}

.footer-block-link {
   display: flex;
   gap: 10px;
}

.footer-block-icon {
   display: flex;
   gap: 10px;
}

.footer-text {

   // .footer-text_small

   &_small {
      font-size: 9px;
      line-height: 25px;
      margin-bottom: 34px;
   }
}